import { useState } from "react"
import { useSelector } from 'react-redux'

// ** Invoice List Sidebar
import Sidebar from './Sidebar'

// ** Columns
import { columns } from './columns'

// ** Store & Actions
import { getAllVendors, getVendorList } from '@store/Vendors/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const VendorsList = () => {
// ** States
  const [currentEntityType, setCurrentEntityType] = useState({ value: '', label: 'Select Entity Type' })
  const [currentStatus, setCurrentStatus] = useState({ value: '', label: 'Select Status' })
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const { allData, data, total } = useSelector(state => state.vendors)
  const { rolesConfig } = useSelector(state => state.roles)
  const { entityType, status } = rolesConfig.vendor

  const entityTypeOptions = [{ value: '', label: 'Select Entity Type' }]
  for (const rKey in entityType) {
    entityTypeOptions.push({ value: rKey, label: entityType[rKey] })
  }
  const statusOptions = [{ value: '', label: 'Select Status' }]
  for (const sKey in status) {
    statusOptions.push({ value: sKey, label: status[sKey] })
  }
  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const customFilters = () => {
    return [
      {
        key: 'entityType',
        options: entityTypeOptions,
        value: currentEntityType,
        onChange: setCurrentEntityType
      },
      {
        key: 'status',
        options: statusOptions,
        value: currentStatus,
        onChange: setCurrentStatus
      }
    ]
  }

  const customSearch = () => {
    return {
      entityType: currentEntityType.value,
      status: currentStatus.value
    }
  }

  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={allData}
        data={data}
        total={total}
        columns={columns}
        filters={customFilters()}
        searchParams={customSearch()}
        getList={getVendorList}
        getAllList={getAllVendors}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New Vendor'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default VendorsList
