// ** React Import
import { useDispatch, useSelector } from 'react-redux'

// ** Custom Components
import Sidebar from '@components/sidebar'
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'

// ** Third Party Components
import classnames from 'classnames'
import { Button, Form, FormGroup } from 'reactstrap'

// ** Store & Actions
import { addVendor, vendorFormChange } from '@store/Vendors/actions'

const SidebarNewVendors = ({
  open,
  toggleSidebar
}) => {
  const { formErrors, isLoading, vendorFormData } = useSelector(state => state.vendors),
    dispatch = useDispatch()
  const { rolesConfig } = useSelector(state => state.roles)

  const handleFormChange = (name, value) => {
    dispatch(vendorFormChange(name, value))
  }

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(addVendor())
  }

  const { entityType } = rolesConfig.vendor
  const entityTypeOptions = Object.keys(entityType).map((rKey) => {
    return (
      <option key={rKey} value={rKey}>{entityType[rKey]}</option>
    )
  })

  return (
    <Sidebar
      size='lg'
      open={open}
      title='New Vendor'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit}>
        <InputGroup
          type={'text'}
          label={'Vendor Name'}
          error={formErrors['vendorName']}
          name={'vendorName'}
          placeholder={'Vendor Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={vendorFormData.vendorName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['vendorName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Owner Name'}
          error={formErrors['ownerName']}
          name={'ownerName'}
          placeholder={'Owner Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          required={true}
          defaultValue={vendorFormData.ownerName}
          className={classnames({ 'is-invalid': formErrors['ownerName'] })}
        />
        <InputGroup
          type={'email'}
          label={'Email'}
          error={formErrors['email']}
          name={'email'}
          placeholder={'Email'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={vendorFormData.email}
          required={true}
          className={classnames({ 'is-invalid': formErrors['email'] })}
        />
        <InputGroup
          type={'select'}
          label={'Country Code'}
          error={formErrors['countryCode']}
          name={'countryCode'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={vendorFormData.countryCode}
          required={true}
          className={classnames({ 'is-invalid': formErrors['countryCode'] })}
        >
          <option value=''>Select Country Code</option>
          <option value='254'>+254 Kenya</option>
        </InputGroup>
        <InputGroup
          type={'number'}
          label={'Contact'}
          error={formErrors['phoneNumber']}
          name={'phoneNumber'}
          placeholder={'Contact'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          required={true}
          defaultValue={vendorFormData.phoneNumber}
          className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
        />
        <InputGroup
          type={'select'}
          label={'Entity Type'}
          error={formErrors['entityType']}
          name={'entityType'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={vendorFormData.entityType}
          className={classnames({ 'is-invalid': formErrors['entityType'] })}
        >
          <option value=''>Select Entity Type</option>
          {entityTypeOptions}
        </InputGroup>
        <FormGroup row className='mr-0 ml-0'>
          <UILoader blocking={isLoading} className='mr-1'>
            <Button type='submit' color='primary'>
              Submit
            </Button>
          </UILoader>
          <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
            Cancel
          </Button>
        </FormGroup>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewVendors
