// ** React Imports
import { useSelector } from "react-redux"

// ** Custom Components
import UserListAvatar from '@components/user-avatar/UserListAvatar'
import TableAction from '@components/table/TableAction'

// ** Store & Actions
import { getVendor } from '@store/Vendors/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge } from 'reactstrap'
import { Archive, Edit2, FileText, Slack, User } from 'react-feather'

import { formatPhone } from '@helper'

// ** Renders Role Columns
const renderRole = row => {
  const roleObj = {
    sole_proprietor: {
      class: 'text-primary',
      icon: User
    },
    company: {
      class: 'text-danger',
      icon: Slack
    }
  }
  const { rolesConfig } = useSelector(state => state.roles)
  const { entityType } = rolesConfig.vendor

  const Icon = roleObj[row.entityType] ? roleObj[row.entityType].icon : Edit2

  return (
    <span className='text-truncate text-capitalize align-middle'>
      <Icon size={18} className={`${roleObj[row.entityType] ? roleObj[row.entityType].class : ''} mr-50`} />
      {entityType[row.entityType]}
    </span>
  )
}

const statusObj = {
  pending: 'light-warning',
  active: 'light-success',
  deactivated: 'light-secondary',
  deleted: 'light-danger'
}

export const columns = [
  {
    name: 'Vendor',
    minWidth: '297px',
    selector: 'fullName',
    sortable: true,
    cell: row => (
      <UserListAvatar
        name={row.vendorName}
        photo={row.profilePictureURL}
        viewHandler={() => store.dispatch(getVendor(row._id))}
        viewLink={`/vendors/view/${row._id}`}
      />
    )
  },
  {
    name: 'Email',
    minWidth: '229px',
    selector: 'email',
    sortable: true,
    cell: row => row.email
  },
  {
    name: 'Phone Number',
    minWidth: '229px',
    selector: 'phoneNumber',
    sortable: true,
    cell: row => <span className='text-capitalize'>
      {formatPhone(row.phoneNumberStr)}
    </span>
  },
  {
    name: 'Entity Type',
    minWidth: '172px',
    selector: 'role',
    sortable: true,
    cell: row => renderRole(row)
  },
  {
    name: 'Status',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => {
      const { rolesConfig } = useSelector(state => state.roles)
      const { status } = rolesConfig.employee
      return (
        <Badge className='text-capitalize' color={statusObj[row.status]} pill>
          {status[row.status]}
        </Badge>
      )
    }
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => {
      const actions = [
        {
          link: `/vendors/view/${row._id}`,
          handler: () => store.dispatch(getVendor(row._id)),
          label: 'Details',
          icon: (<FileText size={14} className='mr-50' />)
        },
        {
          link: `/vendors/edit/${row._id}`,
          handler: () => store.dispatch(getVendor(row._id)),
          label: 'Edit',
          icon: (<Archive size={14} className='mr-50' />)
        }
      ]
      return (
        <TableAction actions={actions} />
      )
    }
  }
]
